import React, { Component } from 'react';
import Page from 'components/LayoutComponents/Page';
// import BtnAddNew from 'components/common/button/BtnAddNew';
import { Card, Row, Table, Col, Button } from 'antd';
import { trainingScore } from 'dump/data';
// import FormTrainingScore from './FormTrainingScore';

const columns = [
  {
    title: 'TT',
    dataIndex: 'ordinal',
    key: 'ordinal',
  },
  {
    title: 'Mã sinh viên',
    dataIndex: 'studentCode',
    key: 'studentCode',
  },

  {
    title: 'Họ và tên',
    dataIndex: 'name',
    key: 'name',
  },
  {
    title: 'Ngày sinh',
    dataIndex: 'birthdate',
    key: 'birthdate',
  },
  {
    title: 'Điểm RL',
    dataIndex: 'trainScore',
    key: 'trainScore',
  },
  {
    title: 'Ghi chú',
    dataIndex: 'note',
    key: 'note',
  },
];

class TrainingScorePage extends Component {
  state = {};

  render() {
    return (
      <Card title="Nhập điểm rèn luyện">
        <Row>
          <Col span={24}>
            <Button type="primary" href="#/training-score/excel">
              Nhập dữ liệu từ Excel
            </Button>
          </Col>
        </Row>
        {/* <Row>
          <Col span={24}>
            <FormTrainingScore />
          </Col>
        </Row> */}
        <Row>
          <Col span={24}>
            <Table bordered dataSource={trainingScore} columns={columns} />
          </Col>
        </Row>
      </Card>
    );
  }
}

export default props => (
  <Page {...props}>
    <TrainingScorePage />
  </Page>
);



